// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias
// Created By HamiXR.

import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import '../static/reset.css'
// 
import VueI18n from 'vue-i18n'
import VeeValidate from 'vee-validate'
import App from './App'
import Home from './components/Home'
import Faculty from './components/Faculty'
import Competition from './components/Competition'
import Constitution from './components/Constitution'
import Application from './components/Application'
import About from './components/About'
//
import ApplyForm from './components/public/applyForm'
import Result from './components/public/Result'
import TripInfo from './components/public/TripInfo'
//
import Enrico from './bio/Enrico'
import Smith from './bio/Smith'
import Cong from './bio/Cong'
import Cristiano from './bio/Cristiano'
import Ferrati from './bio/Ferrati'
import Lorenzo from './bio/Lorenzo'
import Zhaoxiaohong from './bio/Zhaoxiaohong'
import Tamara from './bio/Tamara'
import Asaf from './bio/Asaf'
import Andrea from './bio/Andrea'
import Ingrid from './bio/Ingrid'
import Ivy from './bio/Ivy'
import Mark from './bio/Mark'

Vue.config.productionTip = false;
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(VueI18n);
Vue.use(VeeValidate);


const i18n = new VueI18n({
  locale: 'en',
  // this.$i18n.locale //locale
  messages: {
    'en': require('./language/en.json'),
    'zh': require('./language/zh.json'),
    'it': require('./language/it.json')
  }
});


//
const router = new VueRouter({
  mode: "history",//URL#hash#
  //scroll
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else if (to.hash) {
      return {selector: to.hash}
    } else {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      path: "/",
      component: Home
      // children: [//
      //   {
      //     path: "homeA",
      //     component: HomeA,
      //   }
      // ]
    },
    {
      path: "/faculty",
      component: Faculty
    },
    {
      path: "/Competition",
      component: Competition
    },
    {
      path: "/constitution",
      component: Constitution
    },
    {
      path: "/application",
      component: Application
    },
    {
      path: "/about",
      component: About
    },
    // 
    {
      path: "/application/applyForm",
      component: ApplyForm
    },
    {
      path: "/competition/result",
      component: Result
    },
    {
      path: "/tripInfo",
      component: TripInfo
    },
    // 
    {
      path: "/bio/enrico",
      component: Enrico
    },
    {
      path: "/bio/smith",
      component: Smith
    },
    {
      path: "/bio/cong",
      component: Cong
    },
    {
      path: "/bio/cristiano",
      component: Cristiano
    },
    {
      path: "/bio/ferrati",
      component: Ferrati
    },
    {
      path: "/bio/lorenzo",
      component: Lorenzo
    },
    {
      path: "/bio/zhaoxiaohong",
      component: Zhaoxiaohong
    },
    {
      path: "/bio/tamara",
      component: Tamara
    },
    {
      path: "/bio/asaf",
      component: Asaf
    },
    {
      path: "/bio/andrea",
      component: Andrea
    },
    {
      path: "/bio/ingrid",
      component: Ingrid
    },
    {
      path: "/bio/ivy",
      component: Ivy
    },
    {
      path: "/bio/mark",
      component: Mark
    }
  ]
});
new Vue({
  el: '#app',
  router,
  i18n,
  components: {App},
  template: '<App/>'
});



