,[_vm._v("Lorenzo Di Bella began his piano studies at the age of 5. A promising\n          pupil, he graduated at the Conservatorio Rossini of Pesaro. The following five years he attended the courses\n          of Franco Scala and Lazar Berman at the Piano Academy - Incontri col Maestro of Imola, graduating in 2002,\n          and completed his studies at the National Academy of St. Cecilia in Rome with Sergio Perticaroli, where he\n          graduated on June 2004 with the highest vote.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("His intense musical activities led him to collaborate with many orchestras:\n          Florence Symphony Orchestra, Orchestra Filarmonica Marchigiana, Pescara Symphony Orchestra, Orchestra\n          Pomeriggi Musicali\" of Milan , San Remo Symphony Orchestra, Philharmonie of Bacau, OPorto National\n          Orchestra, National Symphony Orchestra of Ucraine, Nancy Symphony Orchestra, SudWestDeutsche Philarmonie,\n          Villingen-Schwenningen Symphony Orchestra, Philharmonisches Kammerorchester Berlin, Moravian Philarmonic\n          Orchestra, West Bohemia Orchestra and New World Philarmonic. He has collaborated with important conductors:\n          Kirill Karabits, Vassylis Christopoulos, Reinhard Seehafer, Jorge Iwer, Zbink Mller, Vladimir Sirenko,\n          Michael Maciaszczyk, Michel Brousseau, David Crescenzi, Francesco Lanzillotta, Luigi Piovano.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Lorenzo Di Bella has a very active teaching schedule as a main piano\n          professor at the High Music School G.Braga in Teramo and also as artistic director of the Accademia\n          Pianistica delle Marche in Recanati (Italy). He is founder and artistic director of the CIVITANOVA CLASSICA\n          Piano Festival , the \"Mugellini Music Festival\" and \"La Palma d'Oro\" International Piano Competition in San\n          Benedetto del Tronto( Italy).")])])])}]
