"},[_vm._v("Elisi has appeared with the Florence Symphony (Italy); Orchestra Classica\n          de Porto (Portugal); Bay Atlantic Symphony, Greeley Philharmonic, Williamsport Symphony, Pennsylvania\n          Centre, Penn State Philharmonic, Penns Woods, UNLV Chamber and Johns Hopkins Symphony orchestras (USA). He\n          also debuted as soloist/conductor with the Green Valley Festival Chamber Orchestra.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Among Elisis awards are top prizes in the Venice Competition (Italy) and\n          the Oporto International Competition (Portugal). After winning nine first prizes in competitions throughout\n          Italy and the US, and having garnered a dozen of other top prizes and awards, Elisi was selected as a Le\n          Gesse Fellow and performed in Toulouse, France, and New Yorks Weill Recital Hall.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Via Classica, a German radio station, offered a two-hour broadcast of\n          Elisis live recital in Hamburg followed by an interview. Additional radio broadcasts include Montebeni\n          Classica FM (Italy), WCLV Cleveland, UNC, KCNV Nevada Public Radio, and KGCS (USA). He also appeared in a TV\n          broadcast for WPSU and Portuguese national TV.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("An avid chamber musician, Elisi has performed at the Taos and Ravinia\n          Festivals, collaborated with the New Orford String Quartet, and principal players from the Baltimore,\n          Chicago, and American Symphony Orchestras. Among the numerous partnerships, he also performed with\n          violinists Joan Kwuon, Charles Castleman, Qian Zhou, Federico Agostini (I Musicis former leader) and\n          violist Juan-Miguel Hernandez (former member of the Fine Arts Quartet).")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("As a champion of new music, he commissioned and premiered works from\n          composers of many nationalities and performed music of Fedele, Berio at the University of Toronto New Music\n          Festival during Sciarrinos residence. He also premiered Paul Chiharas Two Images, at Weill Hall, as well\n          as his latest piano work, Green Pastures (dedicated to Elisi).")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("A frequent guest at leading music festivals, Elisi appears regularly in\n          such settings as Cincinnati Art of the Piano, Montecito, Lee University, Interlochen Center for the Arts,\n          Texas State University festivals, and the Chautauqua Institution (USA); Valencia International Performance\n          Academy and Festival (Spain); Todi Music Masters, Associazione Umbria classica, Amalfi Coast Festival and\n          AmiCa Fest (Italy); Ameri-China Foundation and He was honored to be the featured artist at the Florida and\n          Oklahoma Music Teachers Association State Conferences and he recently performed at the American Liszt\n          Society Festival at Northwestern University.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Dr. Elisi recently joined the distinguished Faculty of Music of the\n          University of Toronto as an associate professor, having previously taught at the Eastman School, the\n          Pennsylvania State University, and UNLV. His current and former students include prizewinner in\n          competitions; hold teaching posts; performed with orchestras (including the Rochester Philharmonic and\n          Dallas Chamber Symphony); gave debut recitals from New York to Caracas, Paris and Seoul; garnered\n          fellowships and scholarships at summer programs and have been accepted in Artist Diploma (Rice University\n          among others), MM, and DMA programs in prestigious institutions in the US and abroad.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Elisi also presented hundreds of master classes, both in conjunction with\n          his performing engagements, at Northwestern University, Boston University, Cincinnati College-Conservatory,\n          Temple University, the University of Michigan (USA); Taylor Academy, University of British Columbia,\n          University of Western Ontario (Canada); National Conservatory of Lima (Peru); Royal Academy (Denmark);\n          Accademia delle Marche (Italy); Taipei National University of the Arts (Taiwan); China Conservatory,\n          Shanghai and Xinghai conservatories (China); Academy of Performing Arts, Hong Kong; Yong Siew Toh\n          Conservatory, Singapore; Jakarta Conservatory, Indonesia; Seoul National, Yonsei, Hanyang, Ewha Womans, as\n          well as most other major universities in Korea. Elisi also held a guest professorship at the China Zhejiang\n          Art School in Hangzhou, China.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("As an adjudicator, he has been on the juries of the Tremplin International,\n          Canadia Music Competition (finals), Glenn Gould School Concerto, Fred Pattison Award, Toronto Steinway,\n          Pacific Rim Festival competitions (Canada); the Iowa, Peabody Yale Gordon, Julia Crane, Fite Young Artist,\n          SMU Concerto and Dallas International, as well as the Nevada, Maryland, Virginia, and Texas State Music\n          Teachers Associations (USA); OsimosNuova Coppa Pianisti, in Italy; the Ameri-China Foundation Competition\n          in Chengdu, China.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("After studying at the conservatories of Bologna and Florence and the\n          world-renownedIncontri col MaestroInternational Piano Academy of Imola, Elisi worked with Leon Fleisher at\n          the Peabody Institute of the Johns Hopkins University. After graduation he was invited by his mentor to\n          share a recital at the World Piano Pedagogy Conference. Among his other teachers were pianists Lazar Berman\n          and Boris Petrushansky, respectively pupils of Goldenweiser and Neuhaus, as well as Alexander Lonquich,\n          Franco Scala and Giuseppe Fricelli.")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("Elisis recent performances of Bach selected Partitas and Preludes (a topic\n          that gained him the accomplishment of the Yellowbarn Festival Artist-in-Residence in 2018) will be released\n          in an album by MSR Classics. Previously, he recorded another album dedicated to Mozart sonatas and other\n          works and the trio Two Images by Paul Chihara is featured on Albany Records.\n          Enrico Elisi is a Steinway Artist. Heinvites his audience to interact with him and like his page at "),_c('a',{attrs:{"href":"http://www.facebook.com/enricoelisipiano.","target":"_blank"}},[_vm._v("www.facebook.com/enricoelisipiano.")])])])])}]
